@import 'mixins';
@import 'variables';
@import 'desktop';
@import 'responsive';
@import '../fonts/stylesheet.css';

// Basic Styles

html {
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  font-family: $font;
  overflow-x: hidden;
}

p {
  padding: 0;
  margin: 0;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  @media screen and (max-width: 1440px) {
    max-width: 1170px;
  }
  @media screen and (max-width: 1170px) {
    padding-left: 30px;
    padding-right: 30px;
  }
  @media screen and (max-width: 640px) {
    padding-right: 25px;
    padding-left: 25px;
  }
}

.grey {
  background: #FAFAFA;
}

// Default Styles

.default {
  &-button {
    font-family: $font;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
    text-decoration: none !important;
    padding: 9px 30px;
    background: #45A6FF;
    border-radius: 8px;
    border: none;
    outline: none !important;
    cursor: pointer;

    @media screen and (max-width: 640px) {
      font-size: 15px;
      padding: 20px 86px;
    }

    &:hover {
      color: white;
      opacity: 0.8;
    }
  }
  &-title {
    font-family: Gilroy;
    font-style: normal;
    font-weight: bold;
    font-size: 45px;
    line-height: 135%;
    color: #181A1D;
    @media screen and (max-width: 1080px) {
      font-size: 40px;
    }
    @media screen and (max-width: 640px) {
      font-size: 30px;
    }
  }
  &-input {
    width: 100%;
    padding: 18px;
    border: 1.2px solid rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
    border-radius: 5px;
    font-family: $font;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    color: $gray;
    outline: none;
    &:focus {
      color: $black;
    }
  }
  &-select {
    user-select: none;
    position: relative;
    padding: 18px 60px 18px 20px;
    -webkit-appearance: none;
    border: 1.2px solid rgba(0, 0, 0, 0.05);
    box-sizing: border-box;
    border-radius: 5px;
    font-family: $font;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 135%;
    color: $black;
    background: url("../img/icons/caret.svg") no-repeat center right 20px;
    background-size: 13px;
    outline: none;
    &:focus {
      background-image: url("../img/icons/caret02.svg");
    }
  }
  &-subtitle {
    font-family: $font;
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 135%;
    color: $gray;
    @media screen and (max-width: 640px) {
      font-size: 16px;
      line-height: 165%;
    }
    &.mini {
      font-size: 16px;
      @media screen and (max-width: 640px) {
        font-size: 12px;
      }
    }
  }
  &-link {
    font-family: $font;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 135%;
    color: $blue;
    display: flex;
    align-items: center;
    text-decoration: none !important;
    @media screen and (max-width: 640px) {
      font-size: 18px;
    }
    &:hover {
      opacity: 0.7;
    }
    &.decor:after {
      margin-left: 16px;
      content: "";
      display: block;
      width: 28.5px;
      height: 14px;
      background-image: url("../img/icons/arrow.svg");
      background-size: 100%;
    }
  }
}

// Header Styles

.header {
  padding: 30px 0;
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (max-width: 768px) {
      justify-content: flex-start;
    }
  }
  .logo {
    img {
      width: 210px;
      @media screen and (max-width: 640px) {
        width: 144px;
      }
    }
  }
  .default-button {
    @media screen and (max-width: 768px) {
      display: none;
    }
  }
}

.menuBurger {
  display: none;
  margin-left: auto;
  @media screen and (max-width: 768px) {
    display: block;
  }
  img {
    width: 30px;
  }
}

.navigation {
  @media screen and (max-width: 768px) {
    display: none;
  }
  .nav-link {
    font-family: $font;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    color: $black;
    margin-right: 50px;
    &:last-child {
      margin-right: 0;
    }
  }
}

// First Block Styles

.block01 {
  background-image: url("../img/iphone.png");
  background-repeat: no-repeat;
  background-position: bottom right;
  @media screen and (max-width: 1440px) {
    background-size: 50%;
  }
  @media screen and (max-width: 640px) {
    background-size: 365px;
    background-color: transparent;
  }
  .container {
    padding-top: 160px;
    padding-bottom: 180px;
    @media screen and (max-width: 980px) {
      padding-bottom: 350px;
    }
    @media screen and (max-width: 640px) {
      padding-top: 40px;
      padding-bottom: 40px;
    }
  }

  .text-block {
    max-width: 788px;
    width: 100%;
    @media screen and (max-width: 1080px) {
      max-width: 650px;
    }
    @media screen and (max-width: 640px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: none;
    }
    .default-title {
      margin-bottom: 25px;
      max-width: 522px;
      @media screen and (max-width: 640px) {
        text-align: center;
        margin-bottom: 8px;
      }
    }
    .default-subtitle {
      margin-bottom: 87px;
      @media screen and (max-width: 640px) {
        max-width: 320px;
        text-align: center;
        margin-bottom: 260px;
      }
    }
    .block01-button {
      display: inline-block;
      padding: 20px 50px;
      font-weight: 500;
      font-size: 20px;
    }
    .mini {
      text-align: center;
      max-width: 252px;
      @media screen and (max-width: 640px) {
        max-width: 150px;
      }
    }
  }
}

.block-changed {
  .container {
    padding-top: 120px;
    padding-bottom: 144px;
  }
  @media screen and (max-width: 1100px) {
    .container {
      padding-bottom: 180px;
    }
    .text-block {
      max-width: 450px;
    }
  }
  @media screen and (max-width: 980px) {
    .container {
      padding-bottom: 400px;
    }
  }
  @media screen and (max-width: 470px) {
    .container {
      padding-top: 50px;
      padding-bottom: 350px;
    }
  }
  @media screen and (max-width: 375px) {
    .container {
      padding-bottom: 300px;
    }
  }
}

// Stats Styles

.stats {
  padding: 60px 0;
  border-bottom: 1.5px solid #F4F4F4;
  @media screen and (max-width: 640px) {
    padding: 70px 0;
  }
  .stats-list {
    display: flex;
    width: 100%;
    text-align: center;
    justify-content: space-between;
    @media screen and (max-width: 768px) {
      flex-wrap: wrap;
      margin-top: -20px;
      margin-bottom: -20px;
    }
    @media screen and (max-width: 640px) {
      flex-direction: column;
      align-items: center;
    }
    .stats-item {
      width: 33.3%;
      display: flex;
      align-items: center;
      justify-content: center;
      @media screen and (max-width: 768px) {
        width: 50%;
        margin-top: 20px;
        margin-bottom: 20px;
      }
      @media screen and (max-width: 640px) {
        width: 100%;
        margin-top: 0;
        margin-bottom: 78px;

        &:last-child {
          margin-bottom: 0;
        }
      }
      .wrap {
        max-width: 184px;
        width: 100%;
      }
      &:nth-child(2) {
        position: relative;
        &:after {
          content: "";
          display: block;
          position: absolute;
          left: 0;
          width: 1.5px;
          height: 87px;
          background: #F4F4F4;
          @media screen and (max-width: 768px) {
            display: none
          }
        }
        &:before {
          content: "";
          display: block;
          position: absolute;
          right: 0;
          width: 1.5px;
          height: 87px;
          background: #F4F4F4;
          @media screen and (max-width: 768px) {
            display: none
          }
        }
      }
      span.num {
        font-family: $gilroy;
        font-style: normal;
        font-weight: bold;
        font-size: 40px;
        line-height: 48px;
        text-align: center;
        color: #181A1D;
        margin-bottom: 15px;
        display: block;
        @media screen and (max-width: 980px) {
          font-size: 30px;
          margin-bottom: 10px;
        }
        @media screen and (max-width: 640px) {
          font-size: 34px;
          line-height: 40px;
        }
      }
      p.default-subtitle {
        font-size: 18px;
        @media screen and (max-width: 980px) {
          font-size: 16px;
        }
        @media screen and (max-width: 640px) {
          font-size: 16px;
        }
      }
    }
  }
}

// Topics Styles

.topics {
  padding-top: 100px;
  padding-bottom: 125px;
  @media screen and (max-width: 640px) {
    padding-top: 60px;
    padding-bottom: 20px;
  }
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .text-block {
    max-width: 935px;
    width: 100%;
    text-align: center;
    margin: 0 auto 80px;
    .default-title {
      margin-bottom: 25px;
      @media screen and (max-width: 640px) {
        font-size: 24px;
        margin: 0 auto 15px;
        max-width: 250px;
      }
    }
    .default-subtitle {
      @media screen and (max-width: 640px) {
        max-width: 320px;
      }
    }
  }

  .topics-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-top: -50px;
    margin-bottom: -50px;
    @media screen and (max-width: 1110px) {
      justify-content: space-around;
      margin-top: -100px;
    }
    @media screen and (max-width: 640px) {
      margin: 0;
    }
    .topic {
      max-width: 354px;
      width: 100%;
      display: flex;
      align-items: flex-end;
      margin-top: 50px;
      margin-bottom: 50px;
      @media screen and (max-width: 1110px) {
        max-width: none;
        padding: 20px;
        align-items: center;
      }
      @media screen and (max-width: 640px) {
        margin: 0 0 50px;
        padding: 0;
        flex-direction: column;
        align-items: center;
      }
      &:nth-child(1) {
        img {
          width: 178px;
          @media screen and (max-width: 640px) {
            width: 90px;
          }
        }
      }
      &:nth-child(4) {
        max-width: 412px;
        @media screen and (max-width: 1100px) {
          max-width: none !important;
        }
        img {
          width: 178px;
          @media screen and (max-width: 640px) {
            width: 90px;
          }
        }
      }
      &:nth-child(5) {
        img {
          width: 93px;
          @media screen and (max-width: 640px) {
            width: 70px;
          }
        }
      }
      img {
        width: 120px;
        margin-right: 30px;
        @media screen and (max-width: 640px) {
          height: 95px;
          width: auto !important;
          margin-right: 0 !important;
          margin-bottom: 30px;
        }
      }
      &-text {
        .title {
          font-family: $font;
          font-style: normal;
          font-weight: 500;
          font-size: 20px;
          line-height: 135%;
          color: $black;
          margin-bottom: 20px;
          display: block;
          @media screen and (max-width: 640px) {
            font-size: 20px;
            margin-bottom: 14px;
            text-align: center;
          }
        }
        p {
          font-size: 18px;
          @media screen and (max-width: 640px) {
            font-size: 18px;
            max-width: 250px;
            text-align: center;
            line-height: 135%;
          }
        }
      }
    }
  }

  .other-topics {
    display: inline-flex;
    text-align: center;
    margin-top: 70px;
  }
}

// Tutorial Styles

.tutorial {
  padding-top: 100px;
  padding-bottom: 120px;
  background: #f4f4f4;
  @media screen and (max-width: 1000px) {
    padding-top: 70px;
    padding-bottom: 80px;
  }
  .container {

  }
  .default-title {
    text-align: center;
    margin-bottom: 80px;

    @media screen and (max-width: 640px) {
      margin-bottom: 50px;
      font-size: 24px;
      line-height: 145%;
    }
  }

  .tutorial-steps {
    display: flex;
    justify-content: space-between;
    position: relative;
    width: 100%;
    @media screen and (max-width: 1000px) {
      margin-left: -20px;
    }
    @media screen and (max-width: 866px) {
      flex-wrap: wrap;
      margin-left: 0;
      justify-content: space-around;
      margin-top: -20px;
      margin-bottom: -20px;
      &:after {
        display: none !important;
      }
    }
    @media screen and (max-width: 640px) {
      flex-direction: column;
      align-items: center;
    }
    &:after {
      content: "";
      display: block;
      position: absolute;
      left: 50px;
      right: 50px;
      top: 80px;
      height: 5px;
      background: #F5F5F5;
      z-index: 5;
      @media screen and (max-width: 1000px) {
        top: 50px;
        left: 90px;
        right: 90px;
      }
    }
  }
  .tutorial-step {
    position: relative;
    z-index: 6;
    display: flex;
    align-items: center;
    flex-direction: column;
    @media screen and (max-width: 866px) {
      width: 50%;
      margin-bottom: 40px;
      margin-top: 40px;
    }
    @media screen and (max-width: 640px) {
      width: 100%;
      margin-top: 0;
      margin-bottom: 50px;

      &:last-child {
        margin-bottom: 0;
      }
    }
    span {
      text-align: center;
      font-family: $font;
      font-style: normal;
      font-weight: normal;
      font-size: 22px;
      line-height: 135%;
      color: $black;
      max-width: 228px;
      width: 100%;
      margin-top: 50px;
      @media screen and (max-width: 1000px) {
        font-size: 16px;
      }
      @media screen and (max-width: 640px) {
        font-weight: 500;
        font-size: 18px;
        line-height: 135%;
        margin-top: 22px;
      }
    }
    img {
      width: 160px;
      @media screen and (max-width: 1000px) {
        width: 100px;
      }
      @media screen and (max-width: 640px) {
        width: 105px;
      }
    }
  }
}

// Specialists Styles

.specialists {
  padding-top: 100px;
  padding-bottom: 72px;

  .swiper-container {
    width: 100%;

    @media screen and (max-width: 640px) {
      position: relative;
    }

    .swiper-navigation {
      display: none;

      @media screen and (max-width: 640px) {
        width: 100%;
        max-width: 254px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: absolute;
        top: 70px;
        z-index: 30;
        left: 50%;
        transform: translateX(-50%);
      }
    }

    .swiper {
      &-prev, &-next {
        img {
          width: 12px;
        }
      }

      &-prev {
        transform: rotate(180deg);
      }
    }
  }

  @media screen and (max-width: 640px) {
    padding-top: 70px;
    padding-bottom: 100px;
  }
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .text-block {
    text-align: center;
    max-width: 950px;
    margin-bottom: 80px;

    @media screen and (max-width: 640px) {
      margin-bottom: 60px;
    }

    .default-title {
      margin-bottom: 25px;
    }
  }

  .default-link {
    color: $black;

    @media screen and (max-width: 640px) {
      display: none;
    }
  }

  @media screen and (max-width: 640px) {
    .default-link {
      margin-top: 50px;
    }
  }
  @media screen and (max-width: 500px) {
    .default-link {
      font-size: 16px;
      max-width: 60%;
      text-align: center;
      &:after {
        display: none;
      }
    }
  }

  .specialists-group {
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 70px;
    @media screen and (max-width: 1100px) {
      flex-wrap: wrap;
      justify-content: space-around;
    }
    @media screen and (max-width: 640px) {
      flex-wrap: nowrap;
      justify-content: initial;
      margin-bottom: 0;
    }
    .specialist {
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: 330px;
      @media screen and (max-width: 1100px) {
        width: 100%;
        max-width: none;
        margin: 0;
        margin-bottom: 60px;
      }
      @media screen and (max-width: 640px) {
        margin-bottom: 0px;
      }
      .name {
        font-family: $font;
        font-style: normal;
        font-weight: 500;
        font-size: 22px;
        line-height: 135%;
        text-align: center;
        color: $black;
        margin-bottom: 15px;

        @media screen and (max-width: 640px) {
          font-size: 20px;
        }
      }
      .desc {
        font-family: $font;
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        line-height: 135%;
        text-align: center;
        color: $gray;
        @media screen and (max-width: 640px) {
          font-size: 18px;
        }
      }
      img {
        width: 330px;
        margin-bottom: 30px;

        @media screen and (max-width: 640px) {
          width: 150px;
        }
      }
    }
  }
}

// Reviews Styles

.reviews {
  padding: 120px 0;
  @media screen and (max-width: 640px) {
    padding: 0;
  }

  .swiper-container {
    position: relative;
    overflow: hidden;
    padding-bottom: 100px;
    @media screen and (max-width: 640px) {
      padding-bottom: 50px;
    }
    .swiper-pagination {
      position: absolute;
      bottom: 0;
    }
    .swiper-pagination-bullet {
      background: #c3c3c3;
      width: 25px;
      height: 25px;
      margin-right: 23px !important;
      @media screen and (max-width: 640px) {
        width: 15px;
        height: 15px;
        margin-right: 15px;
      }
      @media screen and (max-width: 400px) {
        width: 10px;
        height: 10px;
      }
      &-active {
        background: #45A6FF;
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }
  .default-title {
    text-align: center;
    margin-bottom: 120px;
    @media screen and (max-width: 640px) {
      margin-bottom: 35px;
      font-size: 24px;
    }
  }
  .review {
    border: 1.5px solid rgba(0, 0, 0, 0.05);
    box-sizing: border-box;
    border-radius: 10px;
    padding: 50px 28px 0 28px;
    padding-bottom: 50px;
    display: flex;
    align-items: flex-start;
    min-height: 300px !important;
    @media screen and (max-width: 640px) {
      padding: 40px 40px 45px 40px;
      min-height: 100px !important;
      flex-direction: column;
      align-items: center;
    }
    img {
      width: 100px;
      margin-right: 25px;
      @media screen and (max-width: 640px) {
        width: 70px;
        margin-bottom: 25px;
      }
    }
    .name {
      font-family: $font;
      font-style: normal;
      font-weight: 500;
      font-size: 22px;
      line-height: 135%;
      color: #000000;
      margin-bottom: 14px;
      display: block;
      @media screen and (max-width: 640px) {
        font-size: 20px;
        margin-bottom: 10px;
        text-align: center;
      }
    }
    .review-body {
      font-family: $font;
      font-style: normal;
      font-weight: normal;
      font-size: 20px;
      line-height: 135%;
      color: #000000;
      opacity: 0.5;
      @media screen and (max-width: 640px) {
        font-size: 14px;
        line-height: 155%;
        text-align: center;
      }
    }
  }
}

// Last Block Styles

.last-block {
  padding: 120px 0;
  text-align: center;
  @media screen and (max-width: 700px) {
    background: transparent;
    padding-bottom: 80px;
  }
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .default-title {
    width: 100%;
    max-width: 585px;
    margin-bottom: 25px;
    @media screen and (max-width: 640px) {
      margin-bottom: 8px;
    }
  }
  .default-subtitle {
    margin-bottom: 70px;
    font-weight: normal;
    max-width: 790px;
    @media screen and (max-width: 640px) {
      margin-bottom: 80px;
    }
  }

  .default-button {
    font-size: 22px;
    padding: 20px 50px;

    @media screen and (max-width: 640px) {
      font-size: 15px;
      max-width: 275px;
      width: 100%;
    }
  }
}

// Footer Styles

.footer {
  padding-top: 102px;
  padding-bottom: 140px;
  border-top: 1.5px solid #c4c4c4;
  @media screen and (max-width: 640px) {
    padding-top: 20px;
    padding-bottom: 75px;
    border-top: 0;
  }
  .container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    @media screen and (max-width: 1180px) {
      flex-wrap: wrap;
      padding: 0 20px;
      justify-content: space-around;
    }
    @media screen and (max-width: 768px) {
      justify-content: space-between;
      align-items: center;
    }
    @media screen and (max-width: 640px) {
