// Quiz Styles

.quiz {
  height: 100%;
  padding-top: 110px;
  padding-bottom: 325px;
  @media screen and (max-width: 640px) {
    padding-top: 40px;
    padding-bottom: 120px;
    background: #fff;
  }
  .container {
    flex-direction: column;
    align-items: center;
  }
  .text-block {
    text-align: center;
    max-width: 890px;
    margin: 0 auto 52px;
    @media screen and (max-width: 640px) {
      margin-bottom: 50px;
    }
  }
  .default-title {
    margin-bottom: 25px;
    color: #45A6FF;

    @media screen and (max-width: 640px) {
      margin-bottom: 15px;
    }
  }
  .quiz-body {
    max-width: 848px;
    margin: 0 auto;
    position: relative;
    @media screen and (max-width: 640px) {
      margin:0 -25px;
    }

    &.no-branch {
      .quiz-item {
        display: block;
      }
    }

    .quiz-item {
      background: #FFFFFF;
      border-radius: 10px;
      position: relative;
      // animation: appearance 0.3s ease-in-out;
      display: none;

      @media screen and (max-width: 640px) {
        border: none;
      }

      &:first-child {
        animation: none;
      }

      &.active {
        display: block;
      }

      .quiz-item-header {
        padding: 30px 45px;
        font-family: $font;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 135%;
        color: #181A1D;
        border-bottom: 1px solid #c4c4c4;
        @media screen and (max-width: 640px) {
          padding: 20px 50px;
          border-top: 1px solid #c4c4c4;
        }
      }

      .quiz-item-body {
        display: flex;
        padding: 30px 45px 100px;
        flex-wrap: wrap;
        margin: -10px -17px;
        @media screen and (max-width: 640px) {
          flex-direction: column;
          margin: 0;
          padding: 40px 50px 0;
        }
      }

      .quiz-answer {
        padding: 18px 30px;
        border: 1.2px solid rgba(0, 0, 0, 0.05);
        box-sizing: border-box;
        border-radius: 5px;
        display: flex;
        align-items: center;
        font-family: $font;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 135%;
        color: #181A1D;
        margin: 10px 17px;
        user-select: none;

        @media screen and (max-width: 640px) {
          margin: 0 0 25px;
          padding: 18px 20px;
        }

        &:hover {
          cursor: pointer;
          border-color: #45A6FF;
        }

        &:active .radio-button-decor {
          background: #45A6FF;
        }

        input:checked + .radio-button-decor {
          background: #45A6FF;
        }

        &:last-child {
          margin-right: 0;

          @media screen and (max-width: 640px) {
            margin-bottom: 0;
          }
        }

        input {
          display: none;
        }

        .radio-button-decor {
          background: #C4C4C4;
          min-width: 13px;
          height: 13px;
          border-radius: 13px;
          margin-right: 20px;
        }

      }

      &__continue {
        padding: 15px;
        width: 100%;
        max-width: 150px;
        position: absolute;
        right: 20px;
        bottom: 20px;

        @media screen and (max-width: 640px) {
          position: static;
          margin: 60px auto 0;
          max-width: calc(100% - 100px);
          text-align: center;
          display: block;
          padding: 20px 50px;
        }
      }
    }
  }
}

.quiz-body-class {
  background: #FAFAFA;
  overflow-x: initial;

  @media screen and (max-width: 640px) {
    background: #fff;
  }
}

