// Animation

@keyframes appearance {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes disappearance {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    display: none;
  }
}

// UI Styles

.radio {
  margin-bottom: 0;
  input {
    display: none;
  }
  .radio-button {
    display: block;
    background: #E8E8E8;
    width: 20px;
    height: 20px;
    border-radius: 10px;
    @media screen and (max-width: 640px) {
      width: 16px;
      height: 16px;
      border: 1.5px solid rgba(0, 0, 0, 0.15);
      background: transparent;
    }
  }
  input:checked ~ .radio-button {
    background: #45A6FF;
  }
}

// Modal Styles

.backdrop {
  position: fixed;
  z-index: 100;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.65);
  justify-content: center;
  align-items: center;
  display: none;

  @media screen and (max-width: 640px) {
    padding: 20px;
  }

  &.active {
    display: flex;
  }
}

.thanks-popup {
