.mobile-menu {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.90);
  z-index: 999;
  backdrop-filter: blur(25px);
  align-items: center;
  justify-content: center;
  .mob-nav {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    a {
      display: inline-block;
      color: white;
      font-size: 24px;
      margin-bottom: 10px;
      text-transform: uppercase;
    }
  }
  .close-button {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 30px;
    color: white;
  }
  @media screen and (max-width: 768px) {
    &.active {
      display: flex;
    }
  }
}